import React, {useMemo, useState, useEffect} from 'react';
import {withRouter, Link} from 'react-router-dom';
import accounting from 'accounting';

import Checkbox from './Checkbox';

import edit from '../img/edit.svg';
import './place.css';


const Basket = ({match: {params: {areaId, itemId}}, foodAreas, order}) => {
  //init localStorage
  //localStorage->basketData->ItemId(KFC)->time
  //JSON.parse(localStorage.basketData)[itemId]
  // console.log(JSON.parse(localStorage.basketData).kfc)//access
  if (!localStorage.basketData) { // if storage is empty
    let data = {};
    data[itemId] = {
      faster: true,
      time: '',
      selfService: false
    };
    localStorage.setItem('basketData', JSON.stringify(data))
  }
  if(!JSON.parse(localStorage.basketData)[itemId]){ // if no ItemID data
    let extraData = {...JSON.parse(localStorage.basketData)};
    console.log(extraData);
    extraData[itemId] = {
      faster: true,
      time: '',
      selfService: false
    };
    localStorage.setItem('basketData', JSON.stringify(extraData))
  }

  //get data from local storage
  const getLS = (item) => {
    // let data = JSON.parse(localStorage.basketData)[itemId][item];
    // console.log('getLS:', item, data)
    return JSON.parse(localStorage.basketData)[itemId][item];
  };

  //set to localStorage
  const setLS = (item, value) => {
    let data = JSON.parse(localStorage.basketData);
    data[itemId][item] = value;
    localStorage.setItem('basketData', JSON.stringify(data))
  };

  //init start values
  const [faster, setFaster] =
      useState(getLS('faster') || false);
  const [time, setTime] =
      useState(getLS('time') || '');
  const [selfService, setSelfService]
      = useState(getLS('selfService') || false);

  //useEffect to update localstorage each inputs updates
  useEffect(() => {setLS('faster', faster)}, [faster]);
  useEffect(() => {setLS('time', time)}, [time]);
  useEffect(() => {setLS('selfService', selfService)}, [selfService]);
  // useEffect(() => {
  //   console.log(faster, time, selfService)
  //   setLS('faster', faster)
  //   setLS('time', time)
  //   setLS('selfService', selfService)
  //   console.log('TIME:', JSON.parse(localStorage.basketData)[itemId])
  // }, [faster, time, selfService])


  const area = foodAreas.filter(area => area.id === areaId)[0];
  const item = area.items.filter(item => item.id === itemId)[0];

  const [price, products] = useMemo(() => {
    const foodIds = new Set((item.foods || []).map(item => item.id));

    const products = Object.values(order)
        .filter((value) => {
          const {item: {id}} = value;

          return foodIds.has(id);
        });

    const result = products.reduce((result, value) => {
      const {count, item} = value;
      return result + parseInt(item.price) * parseInt(count);
    }, 0);

    return [accounting.formatNumber(result, 0, ' '), products];
  }, [order, item]);

  return (
      <div className="Place">
        <header className="Place__header">
          <aside className="Place__trz">
            <h1 className="Place__head">
              <Link to="/" className="Place__logo">
                {area.name}
              </Link>
            </h1>
            <Link to="/edit" className="Place__change-tz">
              <img
                  alt="change-profile"
                  src={edit}
              />
            </Link>
          </aside>
        </header>
        <aside className="Place__restoraunt">
          <img
              className="Place__restoraunt-logo"
              alt="Fastfood logo"
              src={item.image}
          />
          <h2
              className="Place__restoraunt-name"
          >
            {item.name}
          </h2>
          <p className="Place__restoraunt-type">
            {item.description}
          </p>
        </aside>
        <div className="Place__products-wrapper">
          <ul className="Place__products">
            {products.map(({item, count}) => (
                <li
                    className="Place__product"
                    key={item.id}
                >
                  <img
                      className="Place__product-logo"
                      alt="Ordered product logo"
                      src={item.image}
                  />
                  <h3
                      className="Place__product-name"
                  >
                    {item.name}
                  </h3>
                  <p
                      className="Place__product-price"
                  >
                    Цена: {item.price}
                  </p>
                  <p
                      className="Place__product-count"
                  >
                    x{count}
                  </p>
                </li>
            ))}
          </ul>
          <Link
              className="Place__change-product"
              to={`/place/${areaId}/${itemId}`}
          >
            Изменить
          </Link>
        </div>
        <div className="Place__choice">
          <h3>Время:</h3>
          <div className="Place__choice-item">
            <span>Как можно быстрее</span>
            <Checkbox
                checked={faster}
                onToggle={() => {
                  if (faster) {
                    setFaster(false);
                    // setTime(getLS('time'))
                  } else {
                    setFaster(true);
                    // setTime('');
                  }
                }}
            />
          </div>
          <div className="Place__choice-item">
            <span>Назначить</span>
            <input
                type="time"
                value={faster ? '' : time}
                onFocus={() => {
                  setFaster(false);
                }}
                onChange={event => {
                  setFaster(false);
                  setTime(event.target.value);
                }}
                onBlur={event => {
                  if (time) {
                    setFaster(false);
                    //TODO issue. the last char
                    // is not saved on "onChange"
                    setTime(event.target.value);
                  }
                }}
            />
          </div>
          <div className="Place__choice-item">
            <h3>С собой</h3>
            <Checkbox checked={selfService} onToggle={() => setSelfService(!selfService)}/>
          </div>
          <div className="Place__choice-item">
            <h3>На месте</h3>
            <Checkbox checked={!selfService} onToggle={() => setSelfService(!setSelfService)}/>
          </div>
        </div>
        <footer className="Place__footer">
          {/*TODO button must be disabled on empty basket*/}
          {
            parseInt(price) > 0
                ? <Link to={`/order/${area.id}/${item.id}`} className="Place__order">
                  Оплатить {price}
                </Link>
                : <div className="Place__order">
                  Корзина пуста
                </div>
          }
        </footer>
      </div>
  );
};

export default withRouter(Basket);
